import React, {useContext} from "react";
import "./certificates.scss";
import {certificatesSection} from "../../portfolio";
import {Fade} from "react-reveal";
import AchievementCard from "../../components/achievementCard/AchievementCard";
import StyleContext from "../../contexts/StyleContext";

export default function Certificates() {
  const {isDark} = useContext(StyleContext);

  if (!certificatesSection.display) {
    return null;
  }

  return (
    <div className="main" id="certificates">
      <div className="certificates-section">
        <Fade bottom duration={1000} distance="20px">
          <div className="certificates-heading-div">
            <h1 className="certificates-heading">
              {certificatesSection.title}
            </h1>
            <p className="certificates-subtitle subTitle">
              {certificatesSection.subtitle}
            </p>
          </div>

          <div className="certificates-cards-div">
            {certificatesSection.achievementsCards &&
              certificatesSection.achievementsCards.map((card, index) => (
                <AchievementCard
                  key={index}
                  cardInfo={card}
                  isDark={isDark}
                />
              ))}
          </div>
        </Fade>
      </div>
    </div>
  );
}
